*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}
html {
	font-size: 62.5%;
	font-family: "Open Sans", sans-serif;
	box-sizing: border-box;
}
body {
}
#root {
	position: absolute;
	min-height: 100%;
	min-width: 100%;
	background-image: url("https://images.pexels.com/photos/2469122/pexels-photo-2469122.jpeg?cs=srgb&dl=astronomy-exploration-free-wallpaper-2469122.jpg&fm=jpg");
	background-size: 105%;
	background-repeat: no-repeat;
}
#footer {
	position: fixed;
	bottom: 0;
	width: 100%;
	height: 30px;
	text-align: center;
	padding: 3px 0px 0px 0;
	background: #2e86c0;
	font-size: 1.35em;
}

.app {
	border: 1px solid #ccc;
	border-radius: 5px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 400px;
	background-color: #f0f0f0;
}
.calculator-screen {
	width: 100%;
	font-size: 5rem;
	height: 80px;
	border: none;
	background-color: #252525;
	color: #fff;
	text-align: right;
	padding-right: 20px;
	padding-left: 10px;
	overflow: hidden;
}
button {
	height: 60px;
	background-color: #fff;
	border-radius: 3px;
	border: 1px solid #c4c4c4;
	background-color: transparent;
	font-size: 2rem;
	color: #333;
	background-image: linear-gradient(
		to bottom,
		transparent,
		transparent 50%,
		rgba(0, 0, 0, 0.04)
	);
	box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.05),
		inset 0 1px 0 0 rgba(255, 255, 255, 0.45),
		inset 0 -1px 0 0 rgba(255, 255, 255, 0.15),
		0 1px 0 0 rgba(255, 255, 255, 0.15);
	text-shadow: 0 1px rgba(255, 255, 255, 0.4);
}

button:hover {
	background-color: #eaeaea;
}

.operator {
	color: #337cac;
}

.all-clear {
	background-color: #ff8c00;
	border-color: #b0353a;
	color: #fff;
}

.all-clear:hover {
	background-color: #ffa500;
}

.equal-sign {
	background-color: #2e86c0;
	border-color: #337cac;
	color: #fff;
	height: 100%;
	grid-row: 2 / 6;
	grid-column: 4 / 5;
}

.equal-sign:hover {
	background-color: #4e9ed4;
}
.calculator-keys {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 20px;
	padding: 20px;
}
